<script>
  import { tick } from 'svelte';
  export let tab;
  export let id;

  export let tabs = [];
  export let activeTabId;

  const componentMap = {
    user: () => import('./UserDetail.svelte'),
    report: () => import('./ReportView.svelte'),
    product: () => import('./ProductDetail.svelte'),
    result: () => import('./ResultDetail.svelte')
  };

  // $: console.log('tab detail2', id);

  let loadedComponents = new Map();
  let currentComponent = null;
  let isLoading = false;
  let error = null;

  // $: if (tab?.type) {
  //   loadComponent(tab.type);
  // }
  $: if (activeTabId) {
    loadComponent(activeTabId);
  }

  async function loadComponent(type) {
    isLoading = true;
    error = null;
    currentComponent = null;

    try {
      const loader = componentMap[type];
      //console.log('Kieu:', typeof loader, type);

      if (typeof loader !== 'function') {
        throw new Error(`Component loader for type "${type}" not found`);
      }

      if (loadedComponents.has(type)) {
        currentComponent = loadedComponents.get(type);
      } else {
        const module = await loader();
        currentComponent = module.default;
        loadedComponents.set(type, currentComponent);
      }
    } catch (e) {
      //console.error(e);
      error = e;
    } finally {
      isLoading = false;
      await tick();
    }
  }
</script>

<!-- {#if activeTabId}
  {#each tabs as tab (tab.id)}
    {#if tab.id === activeTabId}
      {#if tab.type === 'results'}
        <Results {tab} />
      {/if}
    {/if}
  {/each}
{:else}
  <div class="p-4 text-gray-500">Chưa có tab nào mở</div>
{/if} -->

<slot {currentComponent} {tabs} {activeTabId} {tab} {isLoading} {error} {id} />
